h1 {
  line-height: 0px;
}

.App-header {
  position: static;
  background-color: #222;
  padding: 10px;
  color: white;
  text-align: center;
  z-index: 10;
}

.App-title {
  font-size: 1.5em;
}

.flexbox {
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  width: 100%;
}

.slider {
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  right: 20px;
  bottom: 20px;
  z-index: 10;
  padding: 10px;
  background:rgb(0,0,0);
  background:rgba(0,0,0,0.5);
  border-radius: 5px;
}

.slider-input {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.distance {
  padding-right: 10px
}

.motion-slider {
  top: 8px; /*placeholder until css wonkiness gets resolved */
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  display: table-cell;
  vertical-align: middle;
  position: relative;
  width: 200px;
  height: 5px;
  background-color: transparent;
}

/*motion slider styling*/

.ReactMotionSliderInput-Track {
  height: 10px;
  width: 100%;
  background-color: rgb(217,221,226);
  border-radius: 5px;
}

.ReactMotionSliderInput-Track-Inner {
  height: 10px;
  background-color: rgb(217,221,226);
  border-radius: 5px;
}

/*would normally use a post-css pipeline or something to fill these on a real project*/
.ReactMotionSliderInput-Handle {
  background: rgb(255,180,53);
  width: 20px;
  height: 20px;
  border-radius: 10px;
  border-color: rgb(255, 180, 53);
  border-width: 0px;
  -webkit-box-shadow: 2.5px 2.5px 2.5px rgba(76,61,35,0.5);
          box-shadow: 2.5px 2.5px 2.5px rgba(76,61,35,0.5);
  -webkit-transition: background-color .25s, border-color .25s, -webkit-transform .25s;
  transition: background-color .25s, border-color .25s, -webkit-transform .25s;
  -o-transition: background-color .25s, transform .25s, border-color .25s;
  transition: background-color .25s, transform .25s, border-color .25s;
  transition: background-color .25s, transform .25s, border-color .25s, -webkit-transform .25s;
}

.ReactMotionSliderInput-Handle:active {
  background: rgb(255,255,225);
}

.ReactMotionSliderInput-Handle:hover {
  -webkit-transform: scale(1.25);
      -ms-transform: scale(1.25);
          transform: scale(1.25);
  border-color: rgb(49,101,177);
  border-width: 2px;
  border-style: solid;
}

.ReactMotionSliderInput-Range {
  background: rgb(49,101,177);
  border-radius: 5px;
  -webkit-transform: scaleX(1.1);
      -ms-transform: scaleX(1.1);
          transform: scaleX(1.1);
  -webkit-transform-origin: left;
      -ms-transform-origin: left;
          transform-origin: left;
}

/*prev rgb for button 49,137,225 for reference*/


